<template>
	<view class="container pushPlate">
		<room v-if="!ispush"></room>
		<roomSet v-else></roomSet>
		<view class="footer" @click="pushSet" v-if="!ispush">推盘设置</view>
		<view class="footer" @click="sure" v-else>确定</view>
	</view>
</template>

<script>
	import room from '@/pages/salemanagerVisual/house/components/room.vue';
	import roomSet from '@/pages/salemanagerVisual/house/components/roomSet.vue';
	export default {
		data() {
			return {
				ispush:false, // 是否推盘
			}
		},
		methods: {
			// 打开推盘设置
			pushSet(){
				this.ispush = true
			},
			// 确定推盘
			sure(){
				this.ispush = false
			},
		},
		components: {
			room, roomSet
		}
	}
</script>

<style lang="scss">
	.pushPlate{
		padding-bottom: 90rpx;
	}
</style>